(function($) {  
    $.fn.wresize = function(f) {  
        var version = '1.1';  
        var wresize = {  
            fired : false,  
            width : 0  
        };  
 
    function resizeOnce() {
        let msie=/msie/.test(navigator.userAgent.toLowerCase());
        if (msie) {  
            if (!wresize.fired) {  
                wresize.fired = true;  
            }else{  
                version = parseInt(navigator.appVersion, 10);  
                wresize.fired = false;  
                if (version < 7) {  
                    return false;  
                } else if (version == 7) {  
                    // a vertical resize is fired once, an horizontal resize  
                    // twice  
                    var width = $(window).width();  
                    if (width != wresize.width) {  
                            wresize.width = width;  
                            return false;  
                    }  
                }  
            }  
        }  
        return true;  
    }  
 
    function handleWResize(e) { 
        if (resizeTimer) clearTimeout(resizeTimer);
        resizeTimer = setTimeout(function(){
                if (resizeOnce()) { 
                return f.apply(this, [ e ]);  
            }  
        } , 500);
        
    }  
 var resizeTimer = null;
    this.each(function() {  
        if (this == window) {
            $(this).resize(handleWResize);  
            
        } else {  
            $(this).resize(f);  
        }  
    });  
    return this;  
    }; 

})(jQuery);  


